<template>
  <header>
    <span id="header_span">
      <router-link to="/">Inchan's&nbsp; Portfolio</router-link>
    </span>
    <details id="music_details" @click="toggleDetails">
      <summary>Music</summary>
      <ul style="float: left;">
        <li v-for="music in musicList" :key="music.id">
          <div>
            <input type="radio" :id="music.id" name="music" :value="music.url" v-model="selectedMusic" />
            <label :for="music.id">{{ music.name }}</label>
          </div>
        </li>
      </ul>
      <span>
        <input id="play_button" class="music_button" type="button" value="Play" @click="playMusic" />
        <input id="stop_button" class="music_button" type="button" value="Stop" @click="stopMusic" />
      </span>
    </details>
  </header>
</template>

<script>

export default {
  data() {
    return {
      selectedMusic: '../music/Milky.mp3', // 초기값을 노래 중 하나로 설정
      audio: new Audio(),
      musicNodeList: null,
      musicList: [
        { id: 'Milky', name: 'mStone_Milky', url: '../music/Milky.mp3' },
        { id: 'Gatron', name: 'Gatron_Brown', url: '../music/Gatron.mp3' },
        { id: 'Meteor', name: 'SP-star_Meteor_white', url: '../music/Meteor_white.mp3' },
      ],
    };
  },
  methods: {
    playMusic() {
      if (this.selectedMusic) {
        this.audio.src = this.selectedMusic;
        this.audio.load();
        this.audio.addEventListener('canplaythrough', () => {
          this.audio.play();
        });
      }
    },
    stopMusic() {
      this.audio.pause();
    },
    toggleDetails() {
      // details 요소를 토글하는 로직을 추가하세요
    },
  }
};
</script>

<style scoped>
@import './CSS/Header.css';
</style>

